.message {
    margin: 15px 5px;
}

.message .message-username {
    font-size: 13px;
    color: var(--main-text-color);
    opacity: 0.9;
    margin-bottom: 6px;
}

.message .message-text {
    background: var(--main-background-color);
    color: var(--secondary-color);
    display: inline-block;
    padding: 8px 10px;
    border-radius: 8px;
    word-wrap: break-word;
    box-sizing: border-box;
    min-width: 10px;
    max-width: 660px;
}
.message-list {
    grid-area: m;
}

.message-list {
    box-sizing: border-box;
    padding-left: 6px;
    height: 100%;
    overflow-y: auto;
    background: var(--secondary-background-color);
}

.message-list .join-room {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 34px;
    font-weight: 300;
}

.scroll-to-top {
    width: 50px;
    height: 50px;
    position: fixed;
    bottom: -7%;
    right: 3%;
    z-index: 99;
    background: var(--main-background-color);
    border-radius: 50%;
    box-sizing: border-box;
    -webkit-transition: bottom .2s linear;
    transition: bottom .2s linear;
}

.scroll-to-top.visible {
    bottom: 8%;
}

.scroll-to-top:before {
    content: '\276E';
    color: #fff;
    font-size: 30px;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    display: block;
    position: absolute;
    left: 18px;
    top: 11px;
}
.send-message-form {
    grid-area: f;
}

.send-message-form {
    background: var(--send-message-form);
    display: flex;
    position: relative;
    z-index: 100;
}

.send-message-form input {
    width: 100%;
    padding: 15px 10px;
    margin: 0;
    border-style: none;
    background: var(--secondary-color);
    font-weight: 200;
    border-top: 1px solid var(--border-color);
    border-left: 1px solid var(--border-color);
}

.send-message-form input:focus {
    outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
    color: var(--main-text-color);
}

.send-message-form input:-ms-input-placeholder {
    color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
    color: var(--main-text-color);
}

.send-message-form input::placeholder {
    color: var(--main-text-color);
}
@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

.lds-double-ring {
    position: relative;
}

.lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: var(--main-background-color) transparent var(--main-background-color) transparent;
    -webkit-animation: lds-double-ring 1.9s linear infinite;
    animation: lds-double-ring 1.9s linear infinite;
}

.lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent var(--main-background-color) transparent var(--main-background-color);
    -webkit-animation: lds-double-ring_reverse 1.9s linear infinite;
    animation: lds-double-ring_reverse 1.9s linear infinite;
}

.lds-double-ring {
    width: 79px !important;
    height: 79px !important;
    -webkit-transform: translate(-39.5px, -39.5px) scale(0.395) translate(39.5px, 39.5px);
    transform: translate(-39.5px, -39.5px) scale(0.395) translate(39.5px, 39.5px);
}

.spinner {
    margin: 0 auto;
}

.spinner-container {
    width: 100%;
    height: 100%;
}
.rooms-list {
    grid-area: r;
}

.rooms-list {
    box-sizing: border-box;
    background-color: var(--main-color);
    height: 100%;
}

.rooms-list ul {
    list-style-type: none;
    padding: 0;
    margin-bottom: 20px;
}

.rooms-list li {
    padding: 10px 0 10px 10%;
    position: relative;
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}

.rooms-list h3 {
    padding: 30px 0 15px 0;
    color: var(--secondary-color);
    font-size: 22px;
    text-align: center;
    width: 80%;
    border-bottom: 2px solid #45525F;
    margin: 0 auto 30px auto;
}

.rooms-list li.active, .rooms-list li:hover {
    background: #3F4D5B;
}

.rooms-list li:hover.room button {
    color: var(--secondary-color);
}

.rooms-list li.active:before {
    content: '';
    background: var(--main-background-color);
    position: absolute;
    width: 4px;
    height: 100%;
    top: 0;
    left: 0;
}

.rooms-list .room button {
    color: var(--secondary-text-color);
    font-weight: 600;
    text-decoration: none;
    padding: 0;
    border: none;
    background: transparent;
    font-family: 'Roboto', sans-serif;
    font-size: 17px;
}

.rooms-list .room.active button {
    color: var(--secondary-color);
}

.help-text {
    position: relative;
    width: 100%;
    height: 100%;
    overflow-y: auto;
}

.rooms-list.rooms-list-loading {
    display: flex;
    align-items: center;
}
.new-room-form {
    grid-area: n;
}

.new-room-form {
    padding: 0 8px 0 12px;
    background: var(--secondary-color);
    color: var(--main-text-color);
}

.new-room-form form {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.new-room-form input {
    width: 135px;
    background: var(--secondary-color);
}

.new-room-form button {
    background: var(--secondary-color);
    color: var(--main-text-color);
    border: 0;
}

.new-room-form input::-webkit-input-placeholder {
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input:-ms-input-placeholder {
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input::-ms-input-placeholder {
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input::placeholder {
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input:focus {
    outline-width: 0;
}

.new-room-form input {
    border: 0;
}

.new-room-form button {
    border: 0;
}
